/* Geral */
.projects-section {
    padding: 80px 20px;
    background-color: #101010; /* Darker background color */
    color: #ffffff; /* Light text for contrast */
}
  
  .projects-section h2 {
    text-align: center;
    font-size: 2.5rem;
    color: #ffffff;
    margin-bottom: 60px;
    position: relative;
  }
  
  .projects-section h2::after {
    content: "";
    width: 60px;
    height: 4px;
    background-color: #550086;
    display: block;
    margin: 20px auto 0;
    border-radius: 2px;
  }
  
  /* Container de cada projeto */
  .project-container {
    max-width: 1500px; /* Defina uma largura máxima para os projetos */
    margin: 0 auto; /* Centraliza o contêiner de projetos */
    display: flex;
    align-items: center;
    margin-bottom: 80px;
    gap: 60px;
}
  
  .project-container.reverse {
    flex-direction: row-reverse;
  }
  
  /* Imagem do projeto */
  .project-image {
    flex: 1;
  }
  
  .project-image img {
    width: 80%;
    height: auto;
    border-radius: 5px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  .project-image img:hover {
    transform: scale(1.03);
  }
  
  /* Conteúdo do projeto */
  .project-content {
    flex: 1;
    max-width: 600px; /* Define a largura máxima para o texto */
    margin: 100 auto; /* Centraliza o texto dentro do espaço disponível */
}
  
  .project-content h3 {
    font-size: 2rem;
    color: #f1f1f1;
    margin-bottom: 20px;
    text-align: justify;
}
  
  .project-content p {
    font-size: 1.1rem;
    color: #cccccc;
    line-height: 1.6;
    margin-bottom: 30px;
    text-align: justify;
  }
  
  /* Tecnologias utilizadas */
  .project-tech {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top:15px;
    color: #000000;
  }
  
  .tech-item {
    display: flex;
    align-items: center;
    background-color: #f3f3f3;
    padding: 10px 15px;
    border-radius: 8px;
    transition: background-color 0.3s ease;
  }
  
.project-content p {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #d0d0d0;
    margin-bottom: 1px;
    
}
  
  .tech-item:hover {
    background-color: #a5a5a5;
  }
  
  .tech-icon {
    width: 24px;
    height: 24px;
    margin-right: 10px;
  }
  
  /* Responsividade */
  @media (max-width: 992px) {
    .project-container {
      flex-direction: column;
      gap: 20px;
    }
    
    @media (max-width: 768px) {
        .project-container {
          flex-direction: column;
        }
      }
      
  
    .project-container.reverse {
      flex-direction: column;
    }
  
    .project-image,
    .project-content {
      width: 100%;
    }
  
    .project-content h3 {
      text-align: center;
    }
  
    .project-content p {
      text-align: center;
    }
  
    .project-tech {
      justify-content: center;
    }
  }
  